*,
::after,
::before {
  box-sizing: border-box;
}

body,
button,
a,
input {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

input {
  border: none;
  background: transparent;
  outline: none;
}

a {
  text-decoration: none;
}

.content {
  max-width: 1160px;
  margin: 0 auto;
  padding: 0 34px;
}

.header {
  padding: 28px 0;
}

.header__inner {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}

.header__delivery-info {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  margin-left: 7vw;
}

.header__search {
  margin-left: auto;
}

.header__link {
  margin-left: 40px;
  padding: 13px 0;
  white-space: nowrap;
  color: #1f1f1f;
}

.header__link:hover {
  text-decoration: underline;
}

.control {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  padding: 12px 16px;
  border: 1px solid #e0e0e0;
}

.control__icon {
  width: 14px;
  height: 14px;
  margin-right: 10px;
}

.control__input {
  max-height: 24px;
  line-height: 24px;
}

.control + .control {
  margin-left: 20px;
}

.restaurant-card {
  height: 367px;
  width: 100%;
  cursor: pointer;
}

.restaurant-card__img {
  height: 70%;
  width: 100%;
  object-fit: cover;

}

.restaurant-card__title {
  margin: 12px 0 4px;
  font-weight: 500;
}

.restaurant-card__categories {
  margin-bottom: 4px;
  font-size: 14px;
  color: #757575;
}

.restaurant-card__eta {
  padding: 2px 8px;
  background-color: #f5f5f5;
  display: inline-block;
}

.restaurants-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(348px, 1fr));
  gap: 40px 20px;
}
